// @flow

import React, {useState, useRef} from 'react'

import {Container, TopRow, BottomRow, Ball} from './styles'
import {useInterval} from '../../../../../utils/common'

type Props = {|
  onFinish: () => void,
  playing: boolean,
  intervalMs: number,
  radian: number,
|}

const Scene1 = (props: Props) => {
  const {onFinish, playing, intervalMs, radian} = props

  const [idxBall, setIdxBall] = useState<number>(0)
  const cycle = useRef<number>(0)

  useInterval(() => {
    if (!playing) {
      return
    }

    setIdxBall(value => {
      let newValue: number = 0

      newValue = value + 1

      if (newValue === 4) {
        newValue = 0
        cycle.current += 1

        if (cycle.current === 7) {
          onFinish()
        }
      }

      return newValue
    })
  }, intervalMs)

  return (
    <Container>
      <TopRow>
        <Ball radian={radian} activated={idxBall === 0} />
        <Ball radian={radian} activated={idxBall === 2} />
      </TopRow>
      <BottomRow>
        <Ball radian={radian} activated={idxBall === 1} />
        <Ball radian={radian} activated={idxBall === 3} />
      </BottomRow>
    </Container>
  )
}

export default Scene1
