// @flow

import React, {useState, useRef} from 'react'

import {Container, TopRow, BottomRow, Ball} from './styles'
import {useInterval} from '../../../../../utils/common'

type Props = {|
  onFinish: () => void,
  playing: boolean,
  intervalMs: number,
  radian: number,
|}

const Scene1 = (props: Props) => {
  const {onFinish, playing, intervalMs, radian} = props

  const [idxBall, setIdxBall] = useState<number>(0)
  const direction = useRef<boolean>(false)
  const cycle = useRef<number>(0)

  useInterval(() => {
    if (!playing) {
      return
    }

    setIdxBall(value => {
      let newValue: number = 0

      if (direction.current) {
        newValue = value - 1

        if (newValue === -1) {
          newValue = 1
          direction.current = false

          cycle.current += 1

          if (cycle.current === 7) {
            onFinish()
          }
        }
      } else {
        newValue = value + 1

        if (newValue === 13) {
          newValue = 11
          direction.current = true
        }
      }

      return newValue
    })
  }, intervalMs)

  return (
    <Container>
      <TopRow>
        <Ball radian={radian} activated={idxBall === 0} />
        <Ball radian={radian} activated={idxBall === 2} />
        <Ball radian={radian} activated={idxBall === 4} />
        <Ball radian={radian} activated={idxBall === 6} />
        <Ball radian={radian} activated={idxBall === 8} />
        <Ball radian={radian} activated={idxBall === 10} />
        <Ball radian={radian} activated={idxBall === 12} />
      </TopRow>
      <BottomRow>
        <Ball radian={radian} activated={idxBall === 1} />
        <Ball radian={radian} activated={idxBall === 3} />
        <Ball radian={radian} activated={idxBall === 5} />
        <Ball radian={radian} activated={idxBall === 7} />
        <Ball radian={radian} activated={idxBall === 9} />
        <Ball radian={radian} activated={idxBall === 11} />
      </BottomRow>
    </Container>
  )
}

export default Scene1
