// @flow

// import React, {useState, useEffect, useCallback} from 'react'
import React, {useState, useCallback, useEffect} from 'react'
import {ThemeProvider} from 'styled-components'
import {CssBaseline, Typography} from '@material-ui/core'
import {createMuiTheme} from '@material-ui/core/styles'
import {I18n} from 'aws-amplify'
import moment from 'moment'

import Scene0 from './scene-0'
import Scene1 from './scene-1'
import Scene2 from './scene-2'

import {GlobalStyle, Container} from './styles'

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#757ce8',
      main: '#3f50b5',
      dark: '#002884',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#f44336',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
})

const RADIAN_RATE: number = 0.074
const INTERVAL_MS: number = 170

const Se = () => {
  const [idxScene, setIdxScene] = useState<number>(0)
  const [radian, setRadian] = useState<number>(0)

  const handleOnResize = () => {
    setRadian(window.innerHeight * RADIAN_RATE)
  }

  useEffect(() => {
    setRadian(window.innerHeight * RADIAN_RATE)
    window.addEventListener('resize', handleOnResize)

    return () => {
      window.removeEventListener('resize', handleOnResize)
    }
  }, [])

  const handleOnSceneFinish = useCallback(() => {
    if (idxScene === 2) {
      localStorage.setItem('last_date', moment().format('YYYY-MM-DD'))
    }

    setIdxScene(value => value + 1)
  }, [idxScene])

  return (
    <>
      <CssBaseline />
      <GlobalStyle />
      <ThemeProvider theme={theme}>
        {idxScene === 0 ? (
          <Scene0
            radian={radian}
            playing={idxScene === 0}
            onFinish={handleOnSceneFinish}
            intervalMs={INTERVAL_MS}
          />
        ) : null}
        {idxScene === 1 ? (
          <Scene1
            radian={radian}
            playing={idxScene === 1}
            onFinish={handleOnSceneFinish}
            intervalMs={INTERVAL_MS}
          />
        ) : null}
        {idxScene === 2 ? (
          <Scene2
            radian={radian}
            playing={idxScene === 2}
            onFinish={handleOnSceneFinish}
            intervalMs={INTERVAL_MS}
          />
        ) : null}
        {idxScene === 3 ? (
          <Container>
            <Typography variant="h4">{I18n.get('msgEyeTrainingFinish')}</Typography>
          </Container>
        ) : null}
      </ThemeProvider>
    </>
  )
}

export default Se
